import { Component, OnInit,OnDestroy } from '@angular/core';
import { BookService } from '../../book.service';
import { book, wish, messages } from '../../bookInterface';
import { ActivatedRoute,ParamMap } from '@angular/router';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-memberhome',
  templateUrl: './memberhome.component.html',
  styleUrls: ['./memberhome.component.css']
})
export class MemberhomeComponent implements OnInit,OnDestroy {

  constructor(private serv:BookService,private router: ActivatedRoute, private ht:HttpClient) { }
 y:string;
 userid1:string;
 filter:string="filter";
 message12:string;
m:number;
 value:string;
  ngOnInit() {
    this.show();
    this.router.paramMap.subscribe((params:ParamMap )=>{
      this.userid1=params.get(('id'));

    })
    console.log(this.userid1);
    
  }
  x:book[]=[];
  k:messages={
    message:'',
    userid:'',
    selleridEmail:''
  }
  wish1:wish={
    Bookname:'',
    Booksellerid:'',
    useridEmail:''
  }
show()
{
  this.serv.getbook().subscribe(book => {this.x=book;
    console.log(this.x);
   
  })
}
addwish(index){
  console.log(index);
  this.wish1=({
    Bookname:this.x[index].Bookname,
    Booksellerid:this.x[index].sellerEmail,
    useridEmail:this.userid1
    
  });
console.log(this.wish1);
  const val=this.wish1;
console.log(this.wish1);

  this.serv.wishpostdata(val);
}
ngOnDestroy(){
  this.serv.setActiveID(this.userid1)
  console.log(this.userid1)
  console.log(this.serv.getActiveID())
}

 filteration()
 {
   console.log(this.value);
   if(this.filter=='condition')
   {
    this.ht.post('/api/listings/filterbycondition',{condition:this.value}).subscribe((data:any) => {
      this.x=data;
    });
   }
   if(this.filter=='price')
   {
     this.m=+this.value;
    this.ht.post('/api/listings/filterbyprice',{price:this.m}).subscribe((data:any) => {
      this.x=data;
    });
   }
   if(this.filter=='Bookname')
   {
    this.ht.post('/api/listings/filterbyBookname',{Bookname:this.value}).subscribe((data:any) => {
      this.x=data;
    });
   }if(this.filter=='author')
   {
    this.ht.post('/api/listings/filterbyauthor',{author:this.value}).subscribe((data:any) => {
      this.x=data;
    });
   }
 }
 
msg(index)
{

  console.log(this.userid1);
  console.log(this.x[index].sellerEmail);
  this.k=({
    message:this.message12,
    userid:this.userid1,
    selleridEmail:this.x[index].sellerEmail
  });
  const val=this.k;
  this.serv.postmsg(val);
}
}
