import { Component, OnInit } from '@angular/core';
import { messages } from '../../bookInterface';
import { BookService } from '../../book.service';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-messages',
  templateUrl: './messages.component.html',
  styleUrls: ['./messages.component.css']
})
export class MessagesComponent implements OnInit {

  constructor(private serv:BookService,private http:HttpClient) { }
  y:string;
  masg:messages[]=[];
  ngOnInit() {
    this.y=this.serv.getActiveID();
    this.http.post('/api/message1/getmessage',{selleridEmail:this.y}).subscribe((data:any)=>{
      this.masg=data;
      console.log(this.masg);})
  }
}
