import { Component, OnInit ,OnDestroy} from '@angular/core';
import { cred, log } from '../../bookInterface';
import {BookService } from '../../book.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent  implements OnInit,OnDestroy
{
  user:log={
    email:"",
    pass:""
  };

  constructor(private serv:BookService, private route:Router){
  }
  x:cred[]=[];
  i:number;
  count:number;
  ngOnDestroy(){
    this.serv.id=this.user.email;
    console.log(this.serv.id);
  }
  login()
  {
    this.count=0;
    
    for(this.i=0;this.i<this.x.length;this.i++)
    {
      if(this.x[this.i].email==this.user.email)
      {
        if(this.x[this.i].pass==this.user.pass)
        {
          this.count=1;
          break;
        }
      }
    }
    if(this.count==0){
      alert("Invalid User");
    }else{
      this.route.navigate(['/MemberHome',this.user.email]);
    }
  }
  signup()
  {
    this.route.navigate(['/Signup']);
  }
  ngOnInit(){
    this.serv.getsomething().subscribe(cred => {this.x=cred;
      });
  }


}
