import { Routes } from "@angular/router";
import { VisitorComponent } from "./visitor/visitor.component";
import { HomeComponent } from "./visitor/home/home.component";
import { SignupComponent } from "./visitor/signup/signup.component";
import { LoginComponent } from "./visitor/login/login.component";
import { MemberComponent } from "./member/member.component";
import { MemberhomeComponent } from "./member/memberhome/memberhome.component";
import { AddbooksComponent } from "./member/addbooks/addbooks.component";
import { WishlistComponent } from "./member/wishlist/wishlist.component";
import { MessagesComponent } from "./member/messages/messages.component";


export const appRoutes : Routes =[
    {
        path:'Home', component:VisitorComponent,
      children:[{path:'', component:HomeComponent}]
    },
    {
      path:'Signup', component:VisitorComponent,
    children:[{path:'', component:SignupComponent}]
  },
  {
    path:'Login', component:VisitorComponent,
    children:[{path:'', component:LoginComponent}]
  },
  {
    path:'MemberHome/:id', component:MemberComponent,
  children:[{path:'', component:MemberhomeComponent}]
  },
  {
    path:'whishlist', component:MemberComponent,
  children:[{path:'', component:WishlistComponent}]
  },
  {
    path:'message1', component:MemberComponent,
  children:[{path:'', component:MessagesComponent}]
  },
 {
    path:'Addbooks', component:MemberComponent,
    children:[{path:'', component:AddbooksComponent}]
  },
  {
    path:'', redirectTo:'/Home' , pathMatch: 'full'
  },
]