import { Component, OnInit } from '@angular/core';
import { BookService } from '../../book.service';

import {  wish } from '../../bookInterface';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-wishlist',
  templateUrl: './wishlist.component.html',
  styleUrls: ['./wishlist.component.css']
})
export class WishlistComponent implements OnInit {

  constructor(private serv:BookService,private http:HttpClient) { }
  y:string;
  wish1:wish[]=[];
  ngOnInit() {
    this.y=this.serv.getActiveID();
    this.http.post('/api/wishlist/getwishlist',{useridEmail:this.y}).subscribe((data:any)=>{
      this.wish1=data;
      console.log(this.wish1);})
  }

}
